@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 0;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}


/* game previews */
.game-preview {
  padding: 25px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  border-radius: 8px;
}
.game-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.game-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

/* previews */
.preview {
  padding: 25px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  border-radius: 8px;
  
}
.preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}


.large-display {
  max-height: 50%;
  max-width: 100%;

}

